import React from 'react';
import '../App.css';
import logo from "../logos/logo_2a.png";
import { Box, SimpleGrid, Image, Flex, Text, Spacer, HStack, Button, Heading, Center } from '@chakra-ui/react';
import { useBreakpointValue, useMediaQuery } from '@chakra-ui/react';

import Web3 from 'web3';
import { isEmptyObject, connectWeb3, disconnectWeb3 } from './utils/connectWeb3';

function injectUseBreakpointValue(Component) {
  return (props) => {
    const buttonSize = useBreakpointValue({ base: "sm", md: "md" });
    const textSize = useBreakpointValue({ base: "xs", md: "sm" });
    return <Component {...props} buttonSize={buttonSize} textSize={textSize} />
  }
}

class Navbar extends React.Component {

	connectweb3 = async () => {
		const { web3Modal, updateWeb3, toast } = this.props;

		try {
			const provider = await web3Modal.connect();

			if (provider.chainId !== "0x30da5") { // The Chain ID(s) that are supported
				return toast({
					title: 'Wallet Error',
					description: 'Wrong Network, please connect to Milkomeda',
					status: 'error',
					duration: 6000,
					isClosable: true,
				});
			}

			const web3 = new Web3(provider);

			updateWeb3(web3);

			toast({
				title: 'Connected!',
				description: 'Successfully connected to Milkomeda',
				status: 'success',
				duration: 6000,
				isClosable: true,
			})
		} catch {
			toast({
				title: 'Wallet Error',
				description: 'Error when trying to connect to wallet.',
				status: 'error',
				duration: 6000,
				isClosable: true,
			})
		}
	}

	render() {
		const { web3Modal, updateWeb3, toast, web3, buttonSize, textSize } = this.props;

		return (
			<Box className="navbar" height={{ base: '5vh', md: '4vh', lg: '4vh', xl:'5vh', '2xl': '7vh'}}>
				<Flex align="center" justify="space-between" mb={8} mt={{ base: '-3vh', md: '-1vh', lg: '-2vh', xl:'-3vh', '2xl': '-4vh'}}>
					<HStack spacing="2vw">
						<Image className="ImageClass" src={logo} alt="Milkyway" boxSize="4vw" height={{ base: '24vw', md: '10vw', lg: '10vw', xl:'10vw', '2xl': '8vw'}} />
						<Heading size={buttonSize}>Lending</Heading>
					</HStack>
					<Spacer />
					<HStack spacing="1vw">
						<Heading size={textSize}>{isEmptyObject(web3) ? "Not Connected" : "Milkomeda"}</Heading>
						<Heading size={textSize}>EN</Heading>
						<Button size={buttonSize} onClick={() => isEmptyObject(web3) ? connectWeb3(toast, web3Modal, updateWeb3) : disconnectWeb3(updateWeb3, toast)} className="ConnectWeb3">{isEmptyObject(web3) ? "Connect" : "Disconnect"}</Button>
					</HStack>
				</Flex>
			</Box>
		)
	}
}

export default injectUseBreakpointValue(Navbar);