import Web3 from 'web3';

export const chainId = "0x30da5";

export const connectWeb3 = async (toast, web3Modal, updateWeb3) => {
	try {
      const provider = await web3Modal.connect();

      if (provider.chainId !== chainId) { // The Chain ID(s) that are supported
        return toast({
          title: 'Wallet Error',
          description: 'Wrong Network, please connect to Milkomeda',
          status: 'error',
          duration: 6000,
          isClosable: true,
        });
      }

      const web3 = new Web3(provider);

      updateWeb3(web3, provider);

      toast({
          title: 'Connected!',
          description: 'Successfully connected to Milkomeda',
          status: 'success',
          duration: 6000,
          isClosable: true,
        });

    } catch {
      toast({
        title: 'Wallet Error',
        description: 'Error when trying to connect to wallet.',
        status: 'error',
        duration: 6000,
        isClosable: true,
      })
    }
}

export const disconnectWeb3 = (updateWeb3, toast) => { 
	updateWeb3({});
	toast({
        title: 'Disconnected!',
        description: 'Successfully disconnected.',
        status: 'success',
        duration: 6000,
        isClosable: true,
    });
}

export const isEmptyObject = (obj) => Object.keys(obj).length === 0;