import React from 'react';
import './App.css';
import { Box, SimpleGrid } from '@chakra-ui/react';
import { useToast } from '@chakra-ui/react';

import Web3 from 'web3';
import Web3Modal from "web3modal";

import Main from './components/Main';
import Footer from './components/Footer';
import Navbar from './components/Navbar';

import { connectWeb3, chainId } from './components/utils/connectWeb3';

function injectToast(Component) {
  return (props) => {
    const toast = useToast();
    return <Component {...props} toast={toast} />
  }
}

class App extends React.Component {

  state = {
    web3: {},
    web3Modal: {},
    provider: {},
  }

  async componentDidMount() {
    const { toast } = this.props;

    const web3Modal = new Web3Modal({
      cacheProvider: true, // optional
      providerOptions: {}, // required - Empty Object Signifies Metamask only for now...
    });

    this.setState({ web3Modal });

    await connectWeb3(toast, web3Modal, this.updateWeb3);

    // const provider = await web3Modal.connect();

    // const web3 = new Web3(provider);
  }

  updateWeb3 = (web3, provider) => this.setState({ web3, provider });

  render() {
    return (
      <div className="main-app">
        <SimpleGrid overflowY="auto" minChildWidth="100%">
          <Navbar updateWeb3={this.updateWeb3} web3Modal={this.state.web3Modal} toast={this.props.toast} web3={this.state.web3} />
          <Main updateWeb3={this.updateWeb3} web3Modal={this.state.web3Modal} web3={this.state.web3} />
          <Footer />
        </SimpleGrid>
      </div>
      );
  }
}

export default injectToast(App);
