import React from 'react';
import '../App.css';
import { Box, SimpleGrid, Center, HStack, Heading, Divider, Button, VStack, Input, IconButton, Slider, SliderTrack, SliderFilledTrack, SliderThumb, Tooltip, Text, Spacer, Switch } from '@chakra-ui/react';
import { Stat, StatLabel, StatNumber } from '@chakra-ui/react';
// import { ScrollArea } from '@mantine/core';
import { Select } from '@mantine/core';
import { ArrowUpIcon, ArrowDownIcon } from '@chakra-ui/icons';
import { useBreakpointValue, useMediaQuery } from '@chakra-ui/react';

import { isEmptyObject } from './utils/connectWeb3';

import ERC20ABI from './abi/EIP20Interface.json';

import CErc20ABI from './abi/CErc20.json';

import ComptrollerABI from './abi/Comptroller.json';

import PriceOracle from './abi/PriceOracleProxyUSD.json';

// import AssetElement from './Models/AssetElement';

function injectUseBreakpointValue(Component) {
  return (props) => {
    // const buttonSize = useBreakpointValue({ base: "sm", md: "md" });
    // const textSize = useBreakpointValue({ base: "xs", md: "sm" });
    const isMobile = useBreakpointValue({base: true, md: false});
    return <Component {...props} isMobile={isMobile} />
  }
}

const ConditionalWrapper = ({ condition, wrapper, children }) => condition ? wrapper(children) : children;

class Main extends React.Component {
	state = {
		supplyState: "Lend",
		borrowState: "Borrow",
		supplyIconButtonOpen: false,
		borrowIconButtonOpen: false,
		supplySliderValue: 0,
		borrowSliderValue: 0,
		supplySelectedToken: "",
		borowSelectedToken: "",
		tokenBalances: {},
		mTokenBalances: {},
		tokenPrices: {},
		borrowBalances: {},
		exchangeRateMantissas: {},
		accountLiquidity: 0,
		tokenLiquidity: {},
		tokenCollateralFactors: {},
		tokenAPY: {},
		supplyAmount: 0,
		borrowAmount: 0,
		enteredMarkets: [],
		accountShortfall: 0,
	}

	componentDidMount() {
		this.getBalances()
		this.interval = setInterval(() => this.getBalances(), 1000);
	}

	getBalances = () => {
		if (!isEmptyObject(this.props.web3) && this.props.web3._provider.selectedAddress) {
			let tokens = ["WADA", "USDC"];

			for (let token of tokens) {
				this.getUnderlyingTokenBalance(token);
				this.getTokenBalance(token);
				this.getTokenAPY(token);
				this.getBorrowBalance(token);
				this.getTokenLiquidity(token);
				this.getCollateralFactor(token);
				this.getTokenPriceFromOracle(token);
			}

			this.getEnteredMarkets();
			this.getAccountLiquidity();
		}
	}

	getUnderlyingTokenInfo = tokenName => {
		switch (tokenName) {
			case "WADA":
				return "0x65a51E52eCD17B641f8F0D1d56a6c9738951FDC9";
			case "USDC":
				return "0xC12F6Ee5c853393105f29EF0310e61e6B494a70F";
		}
	}

	getUnderlyingTokenBalance = async tokenName => {
		const { web3 } = this.props;
		const { tokenBalances } = this.state;

		let tokenAddress = this.getUnderlyingTokenInfo(tokenName);

		let contract = new web3.eth.Contract(ERC20ABI.abi, tokenAddress);

		let balance = await contract.methods.balanceOf(web3._provider.selectedAddress).call();

		// tokenBalances[tokenName] = 54325 * 10 ** 18; Test

		tokenBalances[tokenName] = balance;

		this.setState({ tokenBalances });
	}

	getUnderlyingTokenDecimals = tokenName => {
		switch (tokenName) {
			case "WADA":
				return 18;
			case "USDC":
				return 6;	
		}
	}

	getTokenInfo = tokenName => {
		switch (tokenName) {
			case "WADA":
				return "0x7D7F58a98e7923578B40fF4C54C701350Db5845C";
			case "USDC":
				return "0xBF2a1ba18bAEA5ac2cD4A3C2Ca0D7484C1dd8651";
		}
	}

	getTokenBalance = async tokenName => {
		const { web3 } = this.props;
		const { mTokenBalances } = this.state;

		let tokenAddress = this.getTokenInfo(tokenName);

		let contract = new web3.eth.Contract(CErc20ABI.abi, tokenAddress);

		let balance = await contract.methods.balanceOfUnderlying(web3._provider.selectedAddress).call();

		// tokenBalances[tokenName] = 54325 * 10 ** 18; Test

		mTokenBalances[tokenName] = balance;

		this.setState({ mTokenBalances });
	}

	getTokenAPY = async tokenName => {
		const { web3 } = this.props;
		const { tokenAPY } = this.state;

		let tokenAddress = this.getTokenInfo(tokenName);

		let contract = new web3.eth.Contract(CErc20ABI.abi, tokenAddress);

		/*
		7884000 blocks per year
		900 blocks per hour
		21600 blocks per day 
		*/

		const ethMantissa = 1e18;
		const blocksPerDay = 21600; // 4 seconds per block
		const daysPerYear = 365;

		const supplyRatePerBlock = await contract.methods.supplyRatePerBlock().call();
		const borrowRatePerBlock = await contract.methods.borrowRatePerBlock().call();

		const supplyAPY = (((Math.pow((supplyRatePerBlock / ethMantissa * blocksPerDay) + 1, daysPerYear))) - 1) * 100;
		const borrowAPY = (((Math.pow((borrowRatePerBlock / ethMantissa * blocksPerDay) + 1, daysPerYear))) - 1) * 100;

		tokenAPY[tokenName] = { supplyAPY, borrowAPY };
	}

	getBorrowLimit = () => {
		const { accountLiquidity, borrowSelectedToken, tokenPrices, tokenCollateralFactors } = this.state;
		const { web3 } = this.props;

		if (web3._provider?.selectedAddress && borrowSelectedToken) {
			let underlyingPrice = tokenPrices[borrowSelectedToken];
			let collateralFactor = tokenCollateralFactors[borrowSelectedToken];

			return `${(accountLiquidity/underlyingPrice * collateralFactor).toFixed(2)}`;
		} else {
			return "-";
		}

	}

	getBorrowLimitTokens = () => {
		const { accountLiquidity, borrowSelectedToken, tokenPrices, tokenCollateralFactors } = this.state;
		const { web3 } = this.props;

		if (web3._provider?.selectedAddress && borrowSelectedToken) {
			let underlyingPrice = tokenPrices[borrowSelectedToken];
			let collateralFactor = tokenCollateralFactors[borrowSelectedToken];

			return (accountLiquidity/underlyingPrice * collateralFactor);
		}
	}

	getUsedBorrowLimit = () => {
		const { web3 } = this.props;
		const { accountLiquidity, borrowSelectedToken, tokenPrices, borrowBalances, tokenCollateralFactors } = this.state;

		if (web3._provider?.selectedAddress && borrowSelectedToken) {
			let underlyingPrice = tokenPrices[borrowSelectedToken];
			let collateralFactor = tokenCollateralFactors[borrowSelectedToken];

			let usedLimit = 0;

			let supportedCTokens = ["WADA", "USDC"];

			supportedCTokens.forEach(t => usedLimit += (borrowBalances[t] / Math.pow(10, this.getUnderlyingTokenDecimals(t))) * tokenPrices[t]);

			let usableBorrowLimit = accountLiquidity * collateralFactor;

			let usedLimitPercentage = usedLimit/usableBorrowLimit * 100;

			return `${usedLimitPercentage.toFixed(2)} %`;
		} else {
			return "- %";
		}
	}

	getOwedBalance = () => {
		const { web3 } = this.props;
		const { borrowSelectedToken, borrowBalances } = this.state;

		if (web3._provider?.selectedAddress && borrowSelectedToken) {
			return `${borrowBalances[borrowSelectedToken] / Math.pow(10, this.getUnderlyingTokenDecimals(borrowSelectedToken))}`
		} else {
			return "-";
		}
	}

	getCollateralDollars = () => {
		const { accountLiquidity, borrowSelectedToken, tokenPrices, tokenCollateralFactors } = this.state;
		const { web3 } = this.props;

		if (web3._provider?.selectedAddress && borrowSelectedToken) {
			return `$ ${(accountLiquidity).toFixed(2)}`;
		} else {
			return "$ -";
		}
	}

	getRepayMaxAmount = () => {
		const { borrowSelectedToken, tokenBalances, borrowBalances} = this.state;
		const { web3 } = this.props;

		if (web3._provider?.selectedAddress && borrowSelectedToken) {
			if (tokenBalances[borrowSelectedToken] / Math.pow(10, this.getUnderlyingTokenDecimals(borrowSelectedToken)) >= borrowBalances[borrowSelectedToken] / Math.pow(10, this.getUnderlyingTokenDecimals(borrowSelectedToken))) {
				return borrowBalances[borrowSelectedToken] / Math.pow(10, this.getUnderlyingTokenDecimals(borrowSelectedToken));
			} else if (borrowBalances[borrowSelectedToken] > 0 && tokenBalances[borrowSelectedToken] / Math.pow(10, this.getUnderlyingTokenDecimals(borrowSelectedToken)) < borrowBalances[borrowSelectedToken] / Math.pow(10, this.getUnderlyingTokenDecimals(borrowSelectedToken))) {
				return tokenBalances[borrowSelectedToken] / Math.pow(10, this.getUnderlyingTokenDecimals(borrowSelectedToken));
			} else {
				return 0;
			}
		} else {
			return 0;
		}
	}

	getBorrowBalance = async tokenName => {
		const { web3 } = this.props;
		const { exchangeRateMantissas, borrowBalances } = this.state;

		let tokenAddress = this.getTokenInfo(tokenName);

		let contract = new web3.eth.Contract(CErc20ABI.abi, tokenAddress);

		const { 2: borrowBalance, 3: exchangeRateMantissa } = await contract.methods.getAccountSnapshot(web3._provider.selectedAddress).call({ from: web3._provider.selectedAddress });

		exchangeRateMantissas[tokenName] = exchangeRateMantissa;
		borrowBalances[tokenName] = borrowBalance;

		this.setState({ exchangeRateMantissas, borrowBalances });
	}

	getAccountLiquidity = async () => {
		const { web3 } = this.props;

		let tokenAddress = ComptrollerABI.address;

		let contract = new web3.eth.Contract(ComptrollerABI.abi, tokenAddress);

		let { 1: accountLiquidity, 2: accountShortfall } = await contract.methods.getAccountLiquidity(web3._provider.selectedAddress).call({ from: web3._provider.selectedAddress });
		accountLiquidity = accountLiquidity / 1e18;

		this.setState({ accountLiquidity, accountShortfall });
	}

	getTokenLiquidity = async tokenName => {
		const { web3 } = this.props;
		const { tokenLiquidity } = this.state;

		let tokenAddress = this.getTokenInfo(tokenName);

		let contract = new web3.eth.Contract(CErc20ABI.abi, tokenAddress);

		const liquidity = await contract.methods.getCash().call({ from: web3._provider.selectedAddress });

		tokenLiquidity[tokenName] = liquidity;

		this.setState({ tokenLiquidity });
	}

	getCollateralFactor = async tokenName => {
		const { web3 } = this.props;
		const { tokenCollateralFactors } = this.state;

		let tokenAddress = this.getTokenInfo(tokenName);

		let comptrollerAddress = ComptrollerABI.address;

		let comptroller = new web3.eth.Contract(ComptrollerABI.abi, comptrollerAddress);

		let { 1: collateralFactor } = await comptroller.methods.markets(tokenAddress).call();
		collateralFactor = (collateralFactor / 1e18); // Convert to percent if displaying

		tokenCollateralFactors[tokenName] = collateralFactor;

		this.setState({ tokenCollateralFactors });
	}

	getTokenPriceFromOracle = async tokenName => {
		const { web3 } = this.props;
		const { tokenPrices } = this.state;

		let tokenAddress = this.getTokenInfo(tokenName);

		let contract = new web3.eth.Contract(PriceOracle.abi, PriceOracle.address);

		let price = await contract.methods.getUnderlyingPrice(tokenAddress).call({ from: web3._provider.selectedAddress });
		price = price / 1e18;

		tokenPrices[tokenName] = price;

		this.setState({ tokenPrices });
	}

	supplyWADA = async () => {
		const { web3 } = this.props;

		const cTokenContract = new web3.eth.Contract(CErc20ABI.abi, this.getTokenInfo("WADA"));
		const underlyingContract = new web3.eth.Contract(ERC20ABI.abi, this.getUnderlyingTokenInfo("WADA"));

		const amount = 1 * Math.pow(10, 18);

		/*await underlyingContract.methods.approve(
		    this.getTokenInfo("WADA"), web3.utils.toBN(amount)
		  ).send({ from: web3._provider.selectedAddress });*/

		await cTokenContract.methods.mint(
		    web3.utils.toBN(amount).toString()
		  ).send({ from: web3._provider.selectedAddress });
	}

	borrowWADA = async () => {
		const { web3 } = this.props;

		const cTokenContract = new web3.eth.Contract(CErc20ABI.abi, this.getTokenInfo("WADA"));

		const amount = 1 * Math.pow(10, 18);

		/*await underlyingContract.methods.approve(
		    this.getTokenInfo("WADA"), web3.utils.toBN(amount)
		  ).send({ from: web3._provider.selectedAddress });*/

		await cTokenContract.methods.borrow(
		    web3.utils.toBN(amount).toString()
		  ).send({ from: web3._provider.selectedAddress });
	}

	getTokenDecimals = () => 8

	getComptrollerAddress = () => ComptrollerABI.address

	enterMarket = async () => {
		const { web3 } = this.props;
		const { supplySelectedToken } = this.state;

		let supplyToken = this.getTokenInfo(supplySelectedToken);

		let tokenAddress = ComptrollerABI.address;

		let contract = new web3.eth.Contract(ComptrollerABI.abi, tokenAddress);

		const enteredMarket = await contract.methods.enterMarkets([supplyToken]).send({ from: web3._provider.selectedAddress });
	}

	exitMarket = async () => {
		const { web3 } = this.props;
		const { supplySelectedToken } = this.state;

		let supplyToken = this.getTokenInfo(supplySelectedToken);

		let tokenAddress = ComptrollerABI.address;

		let contract = new web3.eth.Contract(ComptrollerABI.abi, tokenAddress);

		const exitedMarket = await contract.methods.exitMarket(supplyToken).send({ from: web3._provider.selectedAddress });
	}

	getEnteredMarkets = async () => {
		const { web3 } = this.props;

		let tokenAddress = ComptrollerABI.address;

		let contract = new web3.eth.Contract(ComptrollerABI.abi, tokenAddress);

		const enteredMarkets = await contract.methods.getAssetsIn(web3._provider.selectedAddress).call();

		this.setState({ enteredMarkets });
	}

	supplyAssets = async () => {
		const { web3 } = this.props;
		const { supplyAmount, supplySelectedToken } = this.state;

		const cTokenContract = new web3.eth.Contract(CErc20ABI.abi, this.getTokenInfo(supplySelectedToken));
		const underlyingContract = new web3.eth.Contract(ERC20ABI.abi, this.getUnderlyingTokenInfo(supplySelectedToken));

		const amount = supplyAmount * Math.pow(10, this.getUnderlyingTokenDecimals(supplySelectedToken));

		await underlyingContract.methods.approve(
		    this.getTokenInfo(supplySelectedToken), web3.utils.toBN(amount)
		  ).send({ from: web3._provider.selectedAddress });

		await cTokenContract.methods.mint(
		    web3.utils.toBN(amount).toString()
		  ).send({ from: web3._provider.selectedAddress });
	}

	withdrawAssets = async tokenName => {
		const { web3 } = this.props;
		const { supplyAmount, supplySelectedToken } = this.state;

		const cTokenContract = new web3.eth.Contract(CErc20ABI.abi, this.getTokenInfo(supplySelectedToken));

		const amount = supplyAmount * Math.pow(10, this.getUnderlyingTokenDecimals(supplySelectedToken));

		await cTokenContract.methods.redeemUnderlying(
		    web3.utils.toBN(amount).toString()
		  ).send({ from: web3._provider.selectedAddress });
	}

	borrowAssets = async tokenName => {
		const { web3 } = this.props;
		const { borrowAmount, borrowSelectedToken } = this.state;

		const cTokenContract = new web3.eth.Contract(CErc20ABI.abi, this.getTokenInfo(borrowSelectedToken));

		const amount = borrowAmount * Math.pow(10, this.getUnderlyingTokenDecimals(borrowSelectedToken));

		await cTokenContract.methods.borrow(
		    web3.utils.toBN(amount).toString()
		  ).send({ from: web3._provider.selectedAddress });
	}

	repayAssets = async tokenName => {
		const { web3 } = this.props;
		const { borrowAmount, borrowSelectedToken } = this.state;

		const cTokenContract = new web3.eth.Contract(CErc20ABI.abi, this.getTokenInfo(borrowSelectedToken));
		const underlyingContract = new web3.eth.Contract(ERC20ABI.abi, this.getUnderlyingTokenInfo(borrowSelectedToken));

		const amount = borrowAmount * Math.pow(10, this.getUnderlyingTokenDecimals(borrowSelectedToken));

		await underlyingContract.methods.approve(
		    this.getTokenInfo(borrowSelectedToken), web3.utils.toBN(amount)
		  ).send({ from: web3._provider.selectedAddress });

		await cTokenContract.methods.repayBorrow(
		    web3.utils.toBN(amount).toString()
		  ).send({ from: web3._provider.selectedAddress });
	}

	resetSupplyStates = () => {
		this.setState({
			supplyIconButtonOpen: false,
			supplySliderValue: 0,
			supplySelectedToken: "",
			supplyAmount: 0,
		})
	}

	resetBorrowStates = () => {
		this.setState({
			borrowIconButtonOpen: false,
			borrowSliderValue: 0,
			borrowSelectedToken: "",
			borrowAmount: 0,
		})
	}

	render() {
		return (
			<Box className="app" height={{ base: '110vh', md: '91vh', lg: '90vh', xl:'89vh', '2xl': '83vh'}}>
				<Center>
					<HStack className="MainContainerStatContainer" spacing="11vw" p={3} mt="1vh" mb="-15vh">
						<Stat>
					   		<StatLabel>Total Supply</StatLabel>
					   		<StatNumber>$-</StatNumber>
						</Stat>
						<Button className="MainContainerStatButton">Expand</Button>
						<Stat>
					   		<StatLabel style={{ whiteSpace: "nowrap" }}>Total Borrow</StatLabel>
					   		<StatNumber>$-</StatNumber>
						</Stat>
					</HStack>
				</Center>
				<Center w="100%" h="100%">
					<ConditionalWrapper condition={this.props.isMobile} wrapper={children => <VStack>{children}</VStack>}>
					<VStack className="SupplyStack" height={{ base: '35%', md: '38%', lg: '40%', xl:'40%', '2xl': '55%'}} width={{ base: '100%', md: '40%', lg: '40%', xl:'35%', '2xl': '25%'}} mt={["25vh", "0"]} mr={["0", "5vw"]} mb={["4vh", "0"]}>
						<HStack width="100%">
							<Button className="MainContainerButton" width="50%" onClick={() => { this.setState({ supplyState: "Lend" }); this.resetSupplyStates(); }}>Lend</Button>
							<Button className="MainContainerButton" width="50%" onClick={() => { this.setState({ supplyState: "Withdraw" }); this.resetSupplyStates(); }}>Withdraw</Button>
						</HStack>
						<Box className="SupplyContainer" height="100%" width="100%" mr={["0", "5vw"]} p={["5vw", "0"]}>
							<Center p={5} height="10%" width="100%">
								<Heading className="MainContainerHeading" size="md">{this.state.supplyState === "Lend" ? "Lend Assets" : "Withdraw Assets"}</Heading>
							</Center>
							{/*<ScrollArea style={{ height: "40vh", marginTop: "1vh" }}>
								<AssetElement assetSymbol="" assetIcon="" assetAddress="" />
								<AssetElement assetSymbol="" assetIcon="" assetAddress="" />
							</ScrollArea>*/}
							<Center>
								<Box className="MainContainerFormRepayInfo" width={{ base: '85%', md: '65%', lg: '65%', xl:'65%', '2xl': '50%'}} height="100%" p={2}>
									<HStack>
										<Text fontSize="sm">{this.state.supplyState === "Lend" ? "Balance" : "Supply Balance" }</Text>
										<Spacer />
										<Text fontSize="sm">{this.state.supplyState === "Lend" ? this.state.supplySelectedToken ? (this.state.tokenBalances[this.state.supplySelectedToken] / 10 ** this.getUnderlyingTokenDecimals(this.state.supplySelectedToken)).toFixed(4) : "-" : this.state.supplySelectedToken ? (this.state.mTokenBalances[this.state.supplySelectedToken] / 10 ** this.getUnderlyingTokenDecimals(this.state.supplySelectedToken)).toFixed(4) : "-" }</Text>
									</HStack>
									<HStack>
										<Text fontSize="sm">Deposit APY</Text>
										<Spacer />
										<Text fontSize="sm">{this.state.tokenAPY[this.state.supplySelectedToken] ? this.state.tokenAPY[this.state.supplySelectedToken].supplyAPY.toFixed(4) : "-"} %</Text>
									</HStack>
									{this.state.supplyState === "Lend" ? <HStack>
										<Text fontSize="sm">Reward APY</Text>
										<Spacer />
										<Text fontSize="sm">0.00 %</Text>
									</HStack> : null}
								</Box>
							</Center>
							<Center>
								<Select className="MainContainerSelect" value={this.state.supplySelectedToken} onChange={(supplySelectedToken) => this.setState({ supplySelectedToken })} searchable maxDropdownHeight={200} data={[
									{ value: "WADA", label: "WADA" },
									{ value: "USDC", label: "USDC" },
								]} />
							</Center>
							<Center>
								<VStack>
									<HStack style={{ marginTop: "1vh" }} spacing={5}>
										<Button onClick={this.state.supplyState === "Lend" ? () => this.setState({ supplyAmount: this.state.tokenBalances[this.state.supplySelectedToken] / 10 ** this.getUnderlyingTokenDecimals(this.state.supplySelectedToken), supplySliderValue: 100 }) : () => this.setState({ supplyAmount: this.state.mTokenBalances[this.state.supplySelectedToken] / 10 ** this.getUnderlyingTokenDecimals(this.state.supplySelectedToken), supplySliderValue: 100 })} className="MainContainerFormMax">Max</Button>
										<Input type="number" onChange={(e) => this.setState({ supplyAmount: e.target.value })} value={this.state.supplyAmount} className="MainContainerFormInput" placeholder="Amount" size="md" width={{ base: '20vw', md: '15vw', lg: '10vw', xl:'10vw', '2xl': '5vw'}} />
										<IconButton className="MainContainerFormIconButton" icon={this.state.supplyIconButtonOpen === false ? <ArrowUpIcon /> : <ArrowDownIcon />} onClick={() => this.setState({ supplyIconButtonOpen: !this.state.supplyIconButtonOpen })} />
									</HStack>
									{this.state.supplyIconButtonOpen ? <Slider aria-label='slider-ex-1' min={0} max={100} defaultValue={this.state.supplySliderValue} onChange={(supplySliderValue) => this.state.supplyState === "Lend" ? this.setState({ supplySliderValue, supplyAmount: ((this.state.tokenBalances[this.state.supplySelectedToken]) / 10 ** this.getUnderlyingTokenDecimals(this.state.supplySelectedToken)) * (supplySliderValue * 0.01) }) : this.setState({ supplySliderValue, supplyAmount: ((this.state.mTokenBalances[this.state.supplySelectedToken]) / 10 ** this.getUnderlyingTokenDecimals(this.state.supplySelectedToken)) * (supplySliderValue * 0.01) }) }>
									  <SliderTrack>
									    <SliderFilledTrack className="MainContainerFormSlider" />
									  </SliderTrack>
									  <Tooltip className="MainContainerFormSliderTooltip" color='white' placement='right' isOpen={this.state.supplyIconButtonOpen} label={`${this.state.supplySliderValue}%`}>
									  	<SliderThumb className="MainContainerFormThumb" />
									  </Tooltip>
									</Slider> : null}
									<Button onClick={this.state.supplyState === "Lend" ? this.props.web3._provider?.selectedAddress && this.state.supplySelectedToken && this.state.supplyAmount > 0 && this.state.supplyAmount <= this.state.tokenBalances[this.state.supplySelectedToken] / 10 ** this.getUnderlyingTokenDecimals(this.state.supplySelectedToken) ? this.supplyAssets : null : this.props.web3._provider?.selectedAddress && this.state.supplySelectedToken && this.state.supplyAmount > 0 && this.state.supplyAmount <= this.state.mTokenBalances[this.state.supplySelectedToken] / 10 ** this.getTokenDecimals() ? this.withdrawAssets : null} className="MainContainerButton" width="100%">{this.state.supplyState === "Lend" ? this.props.web3._provider?.selectedAddress ? (this.state.supplySelectedToken ? (this.state.supplyAmount <= 0 || this.state.supplyAmount > this.state.tokenBalances[this.state.supplySelectedToken] / 10 ** this.getUnderlyingTokenDecimals(this.state.supplySelectedToken) ? "Invalid Balance" : "Supply") : "Select a Token") : "Connect Wallet" : this.props.web3._provider?.selectedAddress ? (this.state.supplySelectedToken ? (this.state.supplyAmount <= 0 || this.state.supplyAmount > this.state.mTokenBalances[this.state.supplySelectedToken] / 10 ** this.getTokenDecimals() ? "Invalid Balance" : "Withdraw") : "Select a Token") : "Connect Wallet"}</Button>
								</VStack>
							</Center>
							{this.state.supplyState === "Lend" ? <Center className="MainContainerFormCollateral">
								<Text htmlFor="enableCollateral">Enable Asset as Collateral?</Text>
								<Switch isChecked={this.state.enteredMarkets[this.state.enteredMarkets.indexOf(this.getTokenInfo(this.state.supplySelectedToken))] ?? false} onChange={this.state.supplySelectedToken ? this.state.enteredMarkets[this.state.enteredMarkets.indexOf(this.getTokenInfo(this.state.supplySelectedToken))] ? this.exitMarket : this.enterMarket : null} p={1} id="enableCollateral" />
							</Center> : null}
						</Box>
					</VStack>
					<VStack height={{ base: '55%', md: '38%', lg: '40%', xl:'40%', '2xl': '55%'}} width={{ base: '100%', md: '40%', lg: '40%', xl:'35%', '2xl': '25%'}}>
						<HStack width="100%">
							<Button className="MainContainerButton" width="50%" onClick={() => { this.setState({ borrowState: "Borrow" }); this.resetBorrowStates(); }}>Borrow</Button>
							<Button className="MainContainerButton" width="50%" onClick={() => { this.setState({ borrowState: "Repay" }); this.resetBorrowStates(); }}>Repay</Button>
						</HStack>
						<Box className="BorrowContainer" height="100%" width="100%" p={["5vw", "0"]}>
							<Center p={5} height="10%" width="100%">
								<Heading className="MainContainerHeading" size="md">{this.state.borrowState === "Borrow" ? "Borrow Assets" : "Repay Assets"}</Heading>
							</Center>
							<Center>
								<Box className="MainContainerFormRepayInfo" width={{ base: '85%', md: '65%', lg: '65%', xl:'65%', '2xl': '50%'}} height="100%" p={2}>
									{this.state.borrowState === "Repay" ? <HStack>
										<Text fontSize="sm">You owe </Text>
										<Spacer />
										<Text fontSize="sm">{this.getOwedBalance()}</Text>
									</HStack> : null}
									{this.state.borrowState === "Repay" ? <HStack>
										<Text fontSize="sm">Your Collateral</Text>
										<Spacer />
										<Text fontSize="sm">{this.getCollateralDollars()}</Text>
									</HStack> : null}
									{ this.state.borrowState === "Repay" ? <span style={{ opacity: "0" }}>Spacer</span> : null}
									<HStack>
										<Text fontSize="sm">{this.state.borrowState === "Borrow" ? "Borrow Limit" : "Balance"}</Text>
										<Spacer />
										<Text fontSize="sm">{this.state.borrowState === "Borrow" ? this.getBorrowLimit() : this.props.web3._provider?.selectedAddress && this.state.borrowSelectedToken ? (this.state.tokenBalances[this.state.borrowSelectedToken] / Math.pow(10, this.getUnderlyingTokenDecimals(this.state.borrowSelectedToken))).toFixed(4) : "-"}</Text>
									</HStack>
									<HStack>
										<Text fontSize="sm">{this.state.borrowState === "Borrow" ? "Used Limit" : "Liquidity Available"}</Text>
										<Spacer />
										<Text fontSize="sm">{this.state.borrowState === "Borrow" ? this.getUsedBorrowLimit() : this.props.web3._provider?.selectedAddress && this.state.borrowSelectedToken ? (this.state.tokenLiquidity[this.state.borrowSelectedToken] / Math.pow(10, this.getUnderlyingTokenDecimals(this.state.borrowSelectedToken))).toFixed(2) : "-"}</Text>
									</HStack>
									{this.state.borrowState === "Borrow" ? <HStack>
										<Text fontSize="sm">Reward APY</Text>
										<Spacer />
										<Text fontSize="sm">0.00 %</Text>
									</HStack> : null}
								</Box>
							</Center>
							{/*<ScrollArea style={{ height: "40vh", marginTop: "1vh" }}>
								<AssetElement assetSymbol="" assetIcon="" assetAddress="" />
								<AssetElement assetSymbol="" assetIcon="" assetAddress="" />
							</ScrollArea>*/}
							<Center>
								<Select className="MainContainerSelect" value={this.state.borrowSelectedToken} onChange={(borrowSelectedToken) => this.setState({ borrowSelectedToken })} searchable maxDropdownHeight={200} data={[
									{ value: "WADA", label: "WADA" },
									{ value: "USDC", label: "USDC" },
								]} />
							</Center>
							<Center>
								<VStack>
									<HStack style={{ marginTop: "1vh" }} spacing={5}>
										<Button onClick={this.state.borrowState === "Borrow" ? () => this.setState({ borrowAmount: this.getBorrowLimitTokens(), borrowSliderValue: 100 }) : () => this.setState({ borrowAmount: (this.getRepayMaxAmount()), borrowSliderValue: 100 })} className="MainContainerFormMax">Max</Button>
										<Input type="number" onChange={(e) => this.setState({ borrowAmount: e.target.value })} value={this.state.borrowAmount} className="MainContainerFormInput" placeholder="Amount" size="md" width={{ base: '20vw', md: '15vw', lg: '10vw', xl:'10vw', '2xl': '5vw'}} />
										<IconButton className="MainContainerFormIconButton" icon={this.state.borrowIconButtonOpen === false ? <ArrowUpIcon /> : <ArrowDownIcon />} onClick={() => this.setState({ borrowIconButtonOpen: !this.state.borrowIconButtonOpen })} />
									</HStack>
									{this.state.borrowIconButtonOpen ? <Slider aria-label='slider-ex-1' min={0} max={100} defaultValue={this.state.borrowSliderValue} onChange={(borrowSliderValue) => this.state.borrowState === "Borrow" ? this.setState({ borrowSliderValue, borrowAmount: (this.getBorrowLimitTokens()) * (borrowSliderValue * 0.01) }) : this.setState({ borrowSliderValue, borrowAmount: (this.getRepayMaxAmount()) * (borrowSliderValue * 0.01) }) }>
									  <SliderTrack>
									    <SliderFilledTrack className="MainContainerFormSlider" />
									  </SliderTrack>
									  <Tooltip className="MainContainerFormSliderTooltip" color='white' placement='right' isOpen={this.state.borrowIconButtonOpen} label={`${this.state.borrowSliderValue}%`}>
									  	<SliderThumb className="MainContainerFormThumb" />
									  </Tooltip>
									</Slider> : null}
									<Button onClick={this.state.borrowState === "Borrow" ? this.props.web3._provider?.selectedAddress && this.state.borrowSelectedToken && this.state.borrowAmount > 0 && this.state.borrowAmount <= this.getBorrowLimitTokens() ? this.borrowAssets : null : this.props.web3._provider?.selectedAddress && this.state.borrowSelectedToken && this.state.borrowAmount > 0 && this.state.borrowAmount <= this.getRepayMaxAmount() ? this.repayAssets : null} className="MainContainerButton" width="100%">{this.state.borrowState === "Borrow" ? this.props.web3._provider?.selectedAddress ? (this.state.borrowSelectedToken ? (this.state.borrowAmount <= 0 || this.state.borrowAmount > this.getBorrowLimitTokens() ? "Invalid Balance" : "Borrow") : "Select a Token") : "Connect Wallet" : this.props.web3._provider?.selectedAddress ? (this.state.borrowSelectedToken ? (this.state.borrowAmount <= 0 || this.state.borrowAmount > this.getRepayMaxAmount() ? "Invalid Balance" : "Repay") : "Select a Token") : "Connect Wallet"}</Button>
								</VStack>
							</Center>
						</Box>
					</VStack>
					</ConditionalWrapper>
				</Center>
				<Center mt={["15vh", "-10vh"]}>
					<Button className="MainContainerViewAllButton">View All Assets and Modify Collateral</Button>
				</Center>
			</Box>
		)
	}
}

export default injectUseBreakpointValue(Main);